import classes from "./ProjectGrid.module.css";
import ImageButton from "../components/ImageButton";
import playStore from "../assets/other/playstore.png";
import web from "../assets/other/www.webp";
import github from "../assets/other/github.webp";
import { RudderAnalytics } from "../analytics/rudder";

const ProjectGrid = (props) => {
  const redirredirectHandlerect = (url) => {
    RudderAnalytics.getInstance().track(`Project ${props.data.name} opened`, {
      url,
    });
    window.open(url);
  };

  return (
    <div className={classes.container}>
      <img src={props.data.image} alt={props.data.name} />
      <h3>{props.data.name}</h3>
      <p>{props.data.detail}</p>
      <div className={classes.links}>
        {props.data.playStore && (
          <ImageButton
            src={playStore}
            onClick={redirredirectHandlerect.bind(null, props.data.playStore)}
          />
        )}
        {props.data.webLink && (
          <ImageButton
            src={web}
            onClick={redirredirectHandlerect.bind(null, props.data.webLink)}
          />
        )}
        {props.data.github && (
          <ImageButton
            src={github}
            onClick={redirredirectHandlerect.bind(null, props.data.github)}
          />
        )}
      </div>
    </div>
  );
};

export default ProjectGrid;
