import React, { useEffect, useState } from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import classes from "./AppBar.module.css";

const AppBar = () => {
  const [scrollPosition, setScrollPosition] = useState(0);
  const [expanded, setExpanded] = useState(false);

  const handleScroll = () => {
    const position = window.pageYOffset;
    setScrollPosition(position);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  let navBarClasses = classes.navbar;

  if (expanded) navBarClasses += " " + classes.darkBlack;
  else if (scrollPosition > 4) navBarClasses += " " + classes.black;

  return (
    <Navbar
      className={navBarClasses}
      expand="lg"
      variant="dark"
      fixed="top"
      collapseOnSelect
      onToggle={() => {
        setExpanded((prev) => !prev);
      }}
    >
      <Navbar.Toggle aria-controls="basic-navbar-nav" className="ms-auto" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="ms-auto">
          <Nav.Link href="#about" className={classes.item}>
            About
          </Nav.Link>
          <Nav.Link href="#timeline" className={classes.item}>
            Timeline
          </Nav.Link>
          <Nav.Link href="#skills" className={classes.item}>
            Skills
          </Nav.Link>
          <Nav.Link href="#projects" className={classes.item}>
            Projects
          </Nav.Link>
          <Nav.Link href="#achievements" className={classes.item}>
            Achievements
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default AppBar;
