import ParticlesBg from "particles-bg";
import classes from "./About.module.css";
import Typist from "react-typist";
import { useState } from "react";
import { RudderAnalytics } from "../../analytics/rudder";

const About = () => {
  const [curRole, setCurRole] = useState(0);
  const roles = [
    "Web Developer",
    "Android Developer",
    "Competitive Programmer",
    "Blockchain Enthusiast",
  ];

  const changeRole = () => {
    setCurRole((role) => {
      return (role + 1) % roles.length;
    });
  };

  const redirectToResume = () => {
    RudderAnalytics.getInstance().track("Resume button clicked");

    window.open(
      "https://drive.google.com/file/d/1-pKjMapwd6AK3iVyZ2vnXhtO6Qy8Q1W9/view"
    );
  };

  return (
    <div className={classes.container} id="about">
      <ParticlesBg
        type="cobweb"
        bg={true}
        color="#89cffe"
        num={window.innerWidth / 15}
      />
      <div className={classes.header}>
        Hello, I'm Mihir Khambhati
        <div className={classes.type}>
          I'm a
          <Typist key={curRole} onTypingDone={changeRole}>
            {roles[curRole]}
            <Typist.Backspace count={roles[curRole].length} delay={1000} />
          </Typist>
        </div>
      </div>
      <button className={classes.myButton} onClick={redirectToResume}>
        Resume
      </button>
    </div>
  );
};

export default About;
